import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import AddIcon from '@mui/icons-material/Add'
import styled from '@emotion/styled'
import { Button } from '@mui/material'
import { useNavigate } from 'react-router-dom'
import RouteEnum from 'routes/RouteEnum'

function createData(country: string, toDate: Date, fromDate: Date) {
  return { country, toDate, fromDate }
}

const rows = [
  createData('Japan', new Date(2023, 1, 5), new Date(2023, 4, 12)),
  createData('Ireland', new Date(2023, 2, 5), new Date(2023, 6, 24)),
  createData('Germany', new Date(2023, 5, 17), new Date(2023, 9, 12)),
  createData('Turkey', new Date(2023, 10, 20), new Date(2023, 11, 30)),
  createData('China', new Date(2024, 1, 1), new Date(2024, 4, 11)),
]

const AddRecordDiv = styled.div`
  padding: 25px 25px 25px 0;
`
const AddRecordButton = styled(Button)`
  padding: 10px;
  float: right;
`

const TableDiv = styled.div`
  margin-top: 25px;
  padding: 25px;
`
const StyledTableHead = styled(TableHead)`
  background-color: #f0f0f0;
`
const StyledTableHeadCell = styled(TableCell)`
  font-weight: 900;
`
const StyledTableBodyCell = styled(TableCell)`
  padding-top: 20px;
  padding-bottom: 20px;
`

function HomePage() {
  const navigate = useNavigate()

  return (
    <>
      <AddRecordDiv>
        <AddRecordButton
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => navigate(RouteEnum.DOTS_RECORD.navigate)}
        >
          Add DOTS Record
        </AddRecordButton>
      </AddRecordDiv>
      <TableDiv>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <StyledTableHead>
              <TableRow>
                <StyledTableHeadCell>To</StyledTableHeadCell>
                <StyledTableHeadCell>From</StyledTableHeadCell>
                <StyledTableHeadCell>Country</StyledTableHeadCell>
              </TableRow>
            </StyledTableHead>
            <TableBody>
              {rows.map((row, key) => (
                <TableRow
                  key={key}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  hover
                >
                  <StyledTableBodyCell component="th" scope="row">
                    {row.toDate.toDateString()}
                  </StyledTableBodyCell>
                  <TableCell>{row.fromDate.toDateString()}</TableCell>
                  <TableCell>{row.country}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </TableDiv>
    </>
  )
}

export default HomePage
